import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs'; // Add this import

@Injectable({
  providedIn: 'root',
})
export class InfluxdbService {
  private apiUrl = 'http://localhost:8086/query';
  private database = 'mainflux';
  private headers: HttpHeaders = new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded');

  constructor(private http: HttpClient) {}

  getInfluxData(query: string): Observable<any> {
    const queryParams = `db=${this.database}&q=${query}`;
    const url = `${this.apiUrl}?${queryParams}`;
    return this.http.get(url, { headers: this.headers });
  }

  // ... other service methods
}

