import { Injectable } from '@angular/core';
import { Observable, forkJoin } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { UserData, Village, House } from '../data/users';
import { InfluxdbService } from '../../influxdb.service';

@Injectable()
export class UserService extends UserData {
  constructor(private influxdbService: InfluxdbService) {
    super();
  }

  getVillages(): Observable<Village[]> {
    return this.influxdbService.getInfluxData('SELECT * FROM village').pipe(
      switchMap((villageResponse: any) => {
        const villages = (villageResponse.results[0].series && villageResponse.results[0].series[0].values)
          ? villageResponse.results[0].series[0].values.map((item: any) => ({
              villageId: item[1], // village_id
              name: item[2], // village_name
              picture: 'assets/images/11.png', // default picture
              houses: []
            }))
          : [];

        // Fetch houses data for each village
        const houseQueries = villages.map(village => {
          return this.influxdbService.getInfluxData(`SELECT * FROM house WHERE village_id='${village.villageId}'`).pipe(
            map((houseResponse: any) => {
              const houses = (houseResponse.results[0].series && houseResponse.results[0].series[0].values)
                ? houseResponse.results[0].series[0].values.map((item: any) => ({
                    house_id: item[1], // house_id
                    name: item[2], // house_name
                    village_id: item[3], // village_id
                  }))
                : [];

              village.houses = houses;
            })
          );
        });

        return forkJoin(houseQueries).pipe(map(() => villages));
      })
    );
  }

  getHouses(): Observable<House[]> {
    return this.influxdbService.getInfluxData('SELECT * FROM house').pipe(
      map((houseResponse: any) => {
        const houses = (houseResponse.results[0].series && houseResponse.results[0].series[0].values)
          ? houseResponse.results[0].series[0].values.map((item: any) => ({
              house_id: item[1], // house_id
              name: item[2], // house_name
              village_id: item[3], // village_id
            }))
          : [];

        return houses;
      })
    );
  }

  // You can add other methods as needed for your application.
}
