import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, timer } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { ElectricityChart } from '../data/electricity';
import { forkJoin } from 'rxjs';


@Injectable()
export class ElectricityService {
  private apiUrl = 'http://localhost:8086/query';
  private database = 'mainflux';
  private pollingInterval = 5000; 

  constructor(private http: HttpClient) {}

  getChartData(): Observable<ElectricityChart[]> {
    const query0PAct = 'SELECT value FROM mainflux.autogen.messages WHERE "name" =~ /.*0:PAct.*/ ';
    const query1PAct = 'SELECT value FROM mainflux.autogen.messages WHERE "name" =~ /.*1:PAct.*/ ';

    return timer(0, this.pollingInterval).pipe(
      switchMap(() => {
        const queryParams0PAct = `db=${this.database}&q=${query0PAct}`;
        const queryParams1PAct = `db=${this.database}&q=${query1PAct}`;

        const url0PAct = `${this.apiUrl}?${queryParams0PAct}`;
        const url1PAct = `${this.apiUrl}?${queryParams1PAct}`;

        const data0PAct$ = this.http.get(url0PAct).pipe(
          map((response: any) => {
            return response.results[0].series[0].values.map((value: any) => ({
              label: value[0],
              value: value[1],
            }));
          })
        );

        const data1PAct$ = this.http.get(url1PAct).pipe(
          map((response: any) => {
            return response.results[0].series[0].values.map((value: any) => ({
              label: value[0],
              value: value[1],
            }));
          })
        );

        return forkJoin([data0PAct$, data1PAct$]);
      })
    );
  }
}
